.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

@media (prefers-reduced-motion: no-preference) {
  .oji {
    height: 40vmin;
    color: rgba(240, 151, 18, 0.815);
    animation: ojisuckspin infinite 10s linear;
  }
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 10s linear;
  }
}

.App-header {
  background-color: #be2f1c;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: rgb(52, 223, 18);
}

.siteside {
  background-color: rgb(236, 241, 243);
  min-height: 100vh;
  max-width: 20vh;
  color: rgb(71, 20, 211);
  align-items: center;
  justify-content: center;
  animation: ojisuckspin infinite 1s linear;
}

.App-link {
  color: #1794c5;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes ojisuckspin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(180deg);
  }
}